.container {
  width: 100%;
  max-width: 1440px;
  background-color: hsl(0deg, 100%, 74%);
  background-image: url(../images/bg-intro-desktop.png);
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-family: "Poppins", sans-serif;
  padding-block: 4rem;
  padding-inline: 4rem;
}
.container .introContent {
  color: white;
  max-width: 35rem;
  padding-inline: 1rem;
}
.container .introContent .title {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 3rem;
  padding-block: 2rem;
}
.container .introContent .subTitle {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  padding-block: 2rem;
}
.container .introForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  padding-block: 3rem;
  gap: 1rem;
  max-width: 40rem;
}
.container .introForm .formTitle {
  color: white;
  background-color: hsl(248deg, 32%, 49%);
  border-radius: 0.5rem;
  padding: 1rem;
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}
.container .introForm .formTitle span {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}
.container .introForm #signupForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  gap: 1rem;
  background-color: white;
  padding: 3rem;
  border-radius: 0.5rem;
  width: 100%;
}
.container .introForm #signupForm input {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1px solid hsl(246deg, 25%, 77%);
}
.container .introForm #signupForm input[type=text] {
  padding-inline-start: 2rem;
}
.container .introForm #signupForm input[type=submit] {
  background-color: hsl(154deg, 59%, 51%);
  color: white;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.container .introForm #signupForm .formFooter {
  font-size: 0.7rem;
  text-align: center;
  color: hsl(246deg, 25%, 77%);
}
.container .introForm #signupForm .formFooter .terms {
  font-size: 0.7rem;
  color: hsl(0deg, 100%, 74%);
}