//Layout
$maxWidth_Mobile: 375px;
$maxWidth_Desktop: 1440px;

//Colors
$color_primary_Red: hsl(0, 100%, 74%);
$color_primary_Green: hsl(154, 59%, 51%);
$color_accent_Blue: hsl(248, 32%, 49%);
$color_neutral_darkBlue: hsl(249, 10%, 26%);
$color_neutral_GrayishBlue: hsl(246, 25%, 77%);

//Fonts
$mainFont: 'Poppins', sans-serif;

@mixin regular_mainFont {
    font-family: $mainFont;
    font-weight: 400;
}

@mixin medium_mainFont {
    font-family: $mainFont;
    font-weight: 500;
}

@mixin semiBold_mainFont {
    font-family: $mainFont;
    font-weight: 600;
}

@mixin bold_mainFont {
    font-family: $mainFont;
    font-weight: 700;
}

.container {
    width: 100%;
    max-width: $maxWidth_Desktop;
    background-color: $color_primary_Red;
    background-image: url(../images/bg-intro-desktop.png);
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-family: $mainFont;
    padding-block: 4rem;
    padding-inline: 4rem;
    .introContent {
        color: white;
        max-width: 35rem;
        padding-inline: 1rem;
        .title {
            @include bold_mainFont();
            font-size: 3rem;
            padding-block: 2rem;
        }

        .subTitle {
            @include regular_mainFont();
            padding-block: 2rem;
        }
    }

    .introForm {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: stretch;
        padding-block: 3rem;
        gap: 1rem;
        max-width: 40rem;
        .formTitle {
            color: white;
            background-color: $color_accent_Blue;
            border-radius: .5rem;
            padding: 1rem;
            text-align: center;
            @include regular_mainFont();

            span {
                @include semiBold_mainFont();
            }
        }
        #signupForm {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: stretch;
            gap: 1rem;
            background-color: white;
            padding: 3rem;
            border-radius: .5rem;
            width: 100%;
            input {
                @include semiBold_mainFont();
                padding: 1rem;
                border-radius: .5rem;
                border: 1px solid $color_neutral_GrayishBlue;
                &[type='text'] {
                    padding-inline-start: 2rem;
                }
                &[type='submit'] {
                    background-color: $color_primary_Green;
                    color: white;
                    text-transform: uppercase;
                    letter-spacing: 2px;
                }
            }

            .formFooter {
                font-size: .7rem;
                text-align: center;
                color: $color_neutral_GrayishBlue;
                .terms {
                    font-size: .7rem;
                    color: $color_primary_Red;
                }
            }

        }

    }
}